@import url(https://fonts.googleapis.com/css2?family=Nunito&family=Poppins&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header {
    background-color: rgb(10, 10, 10);
}
@-webkit-keyframes nameAnim {
    0% { color: white; }
    50% { color: rgb(220, 220, 220); }
    100% { color: white; }
}
@keyframes nameAnim {
    0% { color: white; }
    50% { color: rgb(220, 220, 220); }
    100% { color: white; }
}
.name {
    color: rgb(200, 200, 200);
    font-weight: 500;
    display: flex;
    justify-content: center;
    align-items: center;
    white-space: nowrap;
}
.name i {
    display: flex;
    font-size: 6vw;
    padding: 3vh;
    margin-left: 5vw;
    color: rgb(75, 75, 75);
}
.name b {
    font-size: 5vw;
    padding: 3vh;
    color: white;
    -webkit-animation-name: nameAnim;
            animation-name: nameAnim;
    -webkit-animation-duration: 2.5s;
            animation-duration: 2.5s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    font-family: 'Source Code Pro', monospace;
}

.projects-header {
    margin-left: 5vw;
}
.projects-header i {
    font-size: 4vh;
    font-weight: 900;
    display: flex;
    color: gray;
}
.projects-header b {
    color: rgb(200, 200, 200);
    font-family: 'Source Code Pro', monospace;
    font-weight: 100;
}
.projects-line {
    height: 0.1vh;
    background-color: rgb(31, 31, 31);
}
.tab-section {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 4vh;
    overflow-x: hidden;
}
@-webkit-keyframes cardAnim {
    0% { border: 0.6vh solid rgb(20, 20, 20);}
    50% { border: 0.6vh solid rgb(30, 30, 30);
          border-radius: 1.5vh; }
    100% { border: 0.6vh solid rgb(20, 20, 20); }
}
@keyframes cardAnim {
    0% { border: 0.6vh solid rgb(20, 20, 20);}
    50% { border: 0.6vh solid rgb(30, 30, 30);
          border-radius: 1.5vh; }
    100% { border: 0.6vh solid rgb(20, 20, 20); }
}
.Card {
    color: gray;
    background-color: rgb(40, 40, 40);
    border: 0.5vh solid rgb(20, 20, 20);
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    width: 32vw;
    padding: 3vh 2vw;
    border-radius: 2vh;
    font-family: 'Poppins', sans-serif;
    margin-left: 5vh;
    -webkit-animation-name: cardAnim;
            animation-name: cardAnim;
    -webkit-animation-duration: 1.5s;
            animation-duration: 1.5s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
}
@-webkit-keyframes cardTitleAnim {
    0% { color: tomato; }
    50% { color: rgb(255, 86, 56); }
    100% { color: tomato; }
}
@keyframes cardTitleAnim {
    0% { color: tomato; }
    50% { color: rgb(255, 86, 56); }
    100% { color: tomato; }
}
.Card h2 {
    color: tomato;
    font-size: 3vh;
    line-height: 0.1vh;
    font-family: 'Nunito', sans-serif;
    -webkit-animation-name: cardTitleAnim;
            animation-name: cardTitleAnim;
    -webkit-animation-duration: 1.5s;
            animation-duration: 1.5s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
}
.Card h4 {
    color: rgb(100, 100, 100);
    font-size: 1.6vh;
    font-weight: 100;
}
.Card p {
    font-family: 'Nunito', sans-serif;
    font-size: 2.2vh;
    margin-left: 1vw;
}
.Card a {
    text-decoration: none;
    color: dodgerblue;
    font-size: 2.2vh;
}
.Card a:hover {
    font-weight: 900;
}
.card-lang img {
    width: 1.5vw;
    height: auto;
    margin-left: 1vw;
}
.card-nav {
    display: flex;
}
.card-nav-left {
    margin-right: auto;
}
.card-nav-right {
    margin-left: auto;
    order: 2;
}


/* Long Screen */
@media only screen and (max-width: 600px) {
    .name i {
        font-size: 15vw;
        padding: 3vh;
        color: rgb(75, 75, 75);
    }
    .name b {
        font-size: 12vw;
        padding: 3vh;
        color: white;
        font-family: 'Source Code Pro', monospace;
    }
    .tab-section {
        display: contents;
        justify-content: center;
        align-items: center;
        overflow-x: hidden;
    }
    .Card {
        color: gray;
        background-color: rgb(40, 40, 40);
        height: -webkit-fit-content;
        height: -moz-fit-content;
        height: fit-content;
        width: 75vw;
        padding: 3vh 2vw;
        border-radius: 2vh;
        font-family: 'Poppins', sans-serif;
        margin-left: 5vh;
    }
    .Card h2 {
        color: tomato;
        font-size: 2.5vh;
        line-height: 0.1vh;
        font-family: 'Nunito', sans-serif;
    }
    .Card h4 {
        color: rgb(100, 100, 100);
        font-size: 1.2vh;
        font-weight: 100;
    }
    .Card p {
        font-family: 'Nunito', sans-serif;
        font-size: 1.8vh;
        margin-left: 1vw;
    }
    .Card a {
        text-decoration: none;
        color: dodgerblue;
        font-size: 1.8vh;
    }
    .card-lang img {
        width: 4vw;
        height: auto;
        margin-left: 1vw;
    }
    .projects-header i {
        font-size: 2vh;
        font-weight: 900;
        display: flex;
        color: gray;
    }
}
.Footer a {
    padding: 3vw;
}
.fa-envelope {
    color: rgb(212, 70, 56);
}
.fa-github {
    color: rgb(60, 60, 60);
}
.fa-linkedin {
    color: #0e76a8;
}
.fa-twitter {
    color:  #00acee;
}
.fa-instagram {
    color: #dd2a7b;
}
.fa-telegram {
    color: rgb(0, 136, 204);
}

